
.cultivation-status {
  width: 100%;
  // 移除 max-width 限制，填满容器
  margin: 0 auto;
  font-family: 'Microsoft YaHei', 'PingFang SC', sans-serif;
  background: linear-gradient(180deg, rgba(20, 25, 40, 0.95) 0%, rgba(15, 20, 35, 0.98) 100%);
  border: 1px solid rgba(100, 150, 200, 0.3);
  border-radius: 12px;
  overflow: hidden;
  color: #e0e8f0;
  font-size: 14px;
}

// 头部
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background: linear-gradient(90deg, rgba(60, 80, 120, 0.4) 0%, rgba(80, 60, 100, 0.4) 100%);
  border-bottom: 1px solid rgba(100, 150, 200, 0.2);

  .world-time {
    display: flex;
    align-items: center;
    gap: 6px;
    color: #a0b8d0;

    i {
      color: #88aacc;
    }
  }

  .location {
    display: flex;
    align-items: center;
    gap: 8px;

    .danger-badge {
      font-size: 10px;
      padding: 2px 6px;
      border-radius: 10px;
      color: #fff;
    }
  }
}

// 标签导航
.tab-nav {
  display: flex;
  padding: 8px 12px;
  gap: 4px;
  background: rgba(30, 40, 60, 0.5);
  border-bottom: 1px solid rgba(100, 150, 200, 0.15);
  overflow-x: auto;

  &::-webkit-scrollbar {
    display: none;
  }

  .tab-btn {
    flex: 1;
    min-width: 60px;
    padding: 8px 12px;
    background: transparent;
    border: 1px solid transparent;
    border-radius: 6px;
    color: #8090a0;
    font-size: 12px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    white-space: nowrap;

    &:hover {
      background: rgba(100, 150, 200, 0.1);
      color: #b0c0d0;
    }

    &.active {
      background: linear-gradient(135deg, rgba(80, 120, 180, 0.3) 0%, rgba(100, 80, 160, 0.3) 100%);
      border-color: rgba(100, 150, 200, 0.4);
      color: #d0e0f0;
    }
  }
}

// 内容区域
.content {
  min-height: 250px;
  max-height: 500px;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(100, 150, 200, 0.3);
    border-radius: 2px;
  }
}

.panel {
  padding: 16px;
}

// 修炼面板
.cultivation-panel {
  .identity-section {
    text-align: center;
    margin-bottom: 16px;

    .identity-name {
      font-size: 20px;
      font-weight: bold;
      color: #e8f0ff;
      text-shadow: 0 0 10px rgba(100, 150, 255, 0.3);
    }

    .identity-info {
      color: #8090a0;
      font-size: 12px;
      margin-top: 4px;

      .divider {
        margin: 0 6px;
        opacity: 0.5;
      }
    }
  }

  .realm-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    margin-bottom: 16px;

    .realm-title {
      font-size: 18px;
      font-weight: bold;
      display: flex;
      align-items: center;
      gap: 6px;

      i {
        animation: spin 10s linear infinite;
      }
    }

    .realm-status {
      font-size: 12px;
      padding: 3px 8px;
      background: rgba(255, 200, 100, 0.2);
      border: 1px solid rgba(255, 200, 100, 0.4);
      border-radius: 4px;
      color: #ffc864;
    }
  }

  .progress-section {
    margin-bottom: 20px;

    .progress-label {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      color: #8090a0;
      margin-bottom: 6px;
    }

    .progress-bar {
      height: 8px;
      background: rgba(50, 60, 80, 0.8);
      border-radius: 4px;
      overflow: hidden;

      .progress-fill {
        height: 100%;
        border-radius: 4px;
        transition: width 0.5s ease;
        box-shadow: 0 0 10px currentColor;
      }
    }

    .progress-percent {
      text-align: right;
      font-size: 11px;
      color: #6080a0;
      margin-top: 4px;
    }
  }

  .stats-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;

    .stat-item {
      text-align: center;
      padding: 10px 8px;
      background: rgba(40, 50, 70, 0.5);
      border: 1px solid rgba(80, 100, 140, 0.3);
      border-radius: 8px;

      .stat-label {
        font-size: 11px;
        color: #6080a0;
        margin-bottom: 4px;
      }

      .stat-value {
        font-size: 13px;
        font-weight: 500;
        color: #c0d0e0;

        &.gold {
          color: #ffd700;
        }
      }
    }
  }
}

// 神通面板
.skills-panel {
  .skills-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .skill-item {
    padding: 12px;
    background: linear-gradient(135deg, rgba(60, 80, 120, 0.3) 0%, rgba(80, 60, 100, 0.3) 100%);
    border: 1px solid rgba(100, 120, 160, 0.3);
    border-radius: 8px;

    .skill-name {
      font-size: 14px;
      font-weight: bold;
      color: #a0c0ff;
      margin-bottom: 6px;
    }

    .skill-desc {
      font-size: 12px;
      color: #8090a0;
      line-height: 1.5;
    }
  }
}

// 背包面板
.inventory-panel {
  .inventory-section {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .section-title {
    font-size: 14px;
    font-weight: bold;
    color: #a0b8d0;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 6px;

    i {
      color: #88aacc;
    }
  }

  .items-grid {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .item-card {
    padding: 10px 12px;
    background: rgba(40, 50, 70, 0.5);
    border: 1px solid rgba(80, 100, 140, 0.3);
    border-radius: 6px;

    &.treasure {
      border-color: rgba(255, 200, 100, 0.4);
      background: linear-gradient(135deg, rgba(60, 50, 40, 0.5) 0%, rgba(50, 40, 60, 0.5) 100%);
    }

    .item-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    .item-name {
      font-weight: 500;
      color: #c0d0e0;
    }

    .item-count {
      font-size: 12px;
      color: #88aacc;
    }

    .item-rank {
      font-size: 11px;
      color: #ffc864;
      margin: 4px 0;
    }

    .item-desc {
      font-size: 11px;
      color: #6080a0;
    }
  }
}

// 红颜面板
.companions-panel {
  .companions-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .companion-card {
    padding: 12px;
    background: linear-gradient(135deg, rgba(80, 50, 70, 0.3) 0%, rgba(60, 40, 80, 0.3) 100%);
    border: 1px solid rgba(180, 100, 140, 0.3);
    border-radius: 8px;

    .companion-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 8px;

      .companion-name {
        font-size: 15px;
        font-weight: bold;
        color: #ffb0c0;
      }

      .companion-realm {
        font-size: 12px;
      }
    }

    .companion-relation {
      font-size: 12px;
      color: #a08090;
      margin-bottom: 8px;

      .relation-value {
        color: #c0a0b0;
        margin-left: 4px;
      }
    }

    .companion-favor {
      display: flex;
      align-items: center;
      gap: 8px;

      .favor-label {
        font-size: 11px;
        color: #806070;
        min-width: 42px;
      }

      .favor-bar {
        flex: 1;
        height: 6px;
        background: rgba(50, 40, 50, 0.8);
        border-radius: 3px;
        overflow: hidden;

        .favor-fill {
          height: 100%;
          border-radius: 3px;
          transition: width 0.3s ease;
        }
      }

      .favor-value {
        font-size: 12px;
        color: #c0a0b0;
        min-width: 30px;
        text-align: right;
      }
    }
  }
}

// 地图面板
.map-panel {
  .current-location {
    text-align: center;
    padding: 20px;
    background: linear-gradient(135deg, rgba(40, 60, 80, 0.5) 0%, rgba(60, 40, 70, 0.5) 100%);
    border: 1px solid rgba(80, 120, 160, 0.3);
    border-radius: 10px;
    margin-bottom: 16px;

    .location-name {
      font-size: 22px;
      font-weight: bold;
      color: #a0d0ff;
      margin-bottom: 4px;
    }

    .location-layer {
      font-size: 12px;
      color: #88aacc;
      margin-bottom: 10px;
    }

    .location-desc {
      font-size: 13px;
      color: #8090a0;
      line-height: 1.5;
    }
  }

  .connections {
    .connections-title {
      font-size: 13px;
      color: #8090a0;
      margin-bottom: 10px;
    }

    .connections-list {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;

      .connection-item {
        padding: 6px 12px;
        background: rgba(40, 50, 70, 0.5);
        border: 1px solid;
        border-radius: 15px;
        font-size: 12px;
        color: #a0b8d0;
      }
    }
  }
}

// 图鉴面板 - 可翻转卡片
// ============================================================================
// 卡片尺寸配置：修改这里的值可以调整卡片大小
// width: 卡片宽度, height: 卡片高度 (保持 2:3 比例效果最佳)
// ============================================================================
.gallery-panel {
  .gallery-hint {
    text-align: center;
    font-size: 13px;
    color: #6080a0;
    margin-bottom: 20px;
  }

  .gallery-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .flip-card {
    // ⬇️ 修改这里的尺寸可以调整卡片大小
    width: 200px;
    height: 280px;
    perspective: 1000px;
    cursor: pointer;

    &:hover .flip-card-inner {
      box-shadow: 0 12px 35px rgba(100, 150, 255, 0.4);
      transform: scale(1.02);
    }
  }

  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s, box-shadow 0.3s;
    transform-style: preserve-3d;
    border-radius: 12px;
    box-shadow: 0 6px 20px rgba(0, 0, 0, 0.5);
  }

  .flip-card.flipped .flip-card-inner {
    transform: rotateY(180deg);
  }

  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    border-radius: 12px;
    overflow: hidden;
    border: 2px solid rgba(100, 150, 200, 0.3);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .flip-card-front {
    .card-name {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 12px;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.9), transparent);
      color: #fff;
      font-size: 14px;
      font-weight: 500;
      text-align: center;
      text-shadow: 0 2px 4px rgba(0, 0, 0, 0.9);
    }
  }

  .flip-card-back {
    transform: rotateY(180deg);
  }
}

// 空状态提示
.empty-hint {
  text-align: center;
  padding: 20px;
  color: #506070;
  font-style: italic;
}

// 底部
.footer {
  display: flex;
  justify-content: space-around;
  padding: 10px 16px;
  background: rgba(20, 30, 45, 0.8);
  border-top: 1px solid rgba(100, 150, 200, 0.15);

  .footer-item {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 12px;
    color: #8090a0;

    i {
      color: #6080a0;
    }
  }
}

// 动画
@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
